import { Command, Controller } from '@ali/4ever-cangjie';
import { isInsertInline } from '@ali/4ever-utils';
import { isSelectionInMention } from '../utils';
import removeMention from '../commands/removeMention';

export default function onCommand(
  command: Command,
  controller: Controller,
  next: () => void,
) {
  if (isInsertInline(command) && isSelectionInMention(controller.value)) {
    removeMention(controller);
  }
  next();
}
